#main-nav {
  position: fixed;
  top: 0;
  z-index: 1;
  padding: 0 20px 7px 20px;
  width: 100vw;
  background-color: rgba(35, 39, 42, 0.8);
  display: flex; }
  #main-nav .flex-grow {
    flex-grow: 1; }
  #main-nav ul {
    list-style: none;
    display: flex;
    padding: 0px;
    margin: 0px; }
  #main-nav #nav-links li {
    display: inline-block;
    margin: 0px;
    padding: 0px; }
    #main-nav #nav-links li a {
      display: inline-block;
      text-decoration: none;
      background: #23272a;
      color: #86c332;
      padding: 10px 20px;
      margin-right: 7px;
      border-right: 2px solid #86c332;
      border-left: 2px solid #86c332;
      border-bottom: 2px solid #86c332; }
    #main-nav #nav-links li a:hover, #main-nav #nav-links li a:focus {
      background-color: #60892f;
      color: white;
      box-shadow: inset 1px 1px 5px #23272a; }
    #main-nav #nav-links li #a-welcome {
      color: white;
      font-weight: bold; }
  #main-nav ul#connect a {
    display: inline-block;
    margin: 5px 10px;
    text-decoration: none;
    font-size: 2em;
    color: #86c332; }
  #main-nav ul#connect a:hover {
    color: #6b6e70; }

