#projects {
  min-height: 100vh;
  background-color: #60892f;
  background-image: repeating-linear-gradient(rgba(134, 195, 50, 0.1), rgba(134, 195, 50, 0.1), transparent 1px, transparent 4px);
  padding: 50px 10px 50px 10px; }

.proj-flex {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  align-items: center; }

#welcome-section {
  box-sizing: border-box;
  height: 100vh;
  width: 75%;
  margin: 0px auto; }

#welcome-grid {
  background: none;
  height: 100%;
  margin: 0px;
  padding: 0px;
  display: grid;
  grid-template: "greet greet name ." ". tagline tagline tagline";
  grid-gap: 5px; }
  #welcome-grid span {
    display: inline-block;
    text-align: center; }

#w-name {
  font-size: 45px;
  margin: 0px;
  padding: 10px 0px;
  text-align: center;
  grid-area: name;
  align-self: end;
  background: #6b6e70;
  color: #23272a; }

#w-greeting {
  font-size: 20vh;
  grid-area: greet;
  align-self: end;
  background: #86c332;
  color: #23272a; }

#w-tagline {
  padding: 10px 0px;
  font-weight: bold;
  font-size: 20px;
  grid-area: tagline;
  align-self: start;
  background: #60892f;
  color: #23272a; }

@media only screen and (max-width: 650px) {
  #welcome-grid {
    grid-template: "." "greet" "." "name" "tagline";
    grid-template-rows: .25fr auto .25fr auto auto;
    align-content: center; } }

@media only screen and (max-width: 380px) {
  #w-name {
    font-size: 30px; }
  #w-tagline {
    font-size: 10px; } }

#inline-viewport {
  position: fixed;
  top: 0;
  left: 0;
  background-color: rgba(35, 39, 42, 0.8);
  height: 100vh;
  width: 100vw;
  padding: 0 5vw 5vh 5vw;
  z-index: 100;
  display: grid;
  grid-template: "inlineHeader" "inlineContent";
  grid-template-rows: 50px 1fr; }

#inline-header {
  padding: 0;
  height: 50px;
  width: 100%;
  background-color: rgba(35, 39, 42, 0.8);
  color: #86c332;
  grid-area: inlineHeader;
  display: flex;
  border-bottom: 2px solid #86c332; }
  #inline-header h3 {
    margin: 0;
    height: 100%;
    display: inline-block;
    background-color: #23272a;
    padding: 10px 20px;
    flex-grow: 1; }
  #inline-header #inline-close {
    display: inline-block;
    border-left: 2px solid #86c332;
    color: #86c332;
    font-size: 20px;
    padding: 10px 20px;
    background-color: #23272a; }
  #inline-header #inline-close:hover {
    background-color: #60892f;
    color: white;
    box-shadow: inset 1px 1px 5px #23272a;
    cursor: pointer; }

#inline-content {
  grid-area: inlineContent;
  padding: 0;
  margin: 0;
  width: 100%;
  height: 100%;
  background-color: #23272a;
  overflow-y: auto;
  overflow-x: hidden; }

iframe {
  width: 100%;
  height: 100%;
  border: none; }

.popup {
  position: relative;
  height: 100%;
  width: 100%;
  display: grid;
  place-items: center; }

.popup-label {
  cursor: pointer; }

.popup-content {
  background-color: #23272a;
  border: 1px solid #6b6e70;
  padding: 5px 15px 5px 5px;
  border-radius: 4px;
  z-index: 100;
  font-size: 14px; }

* {
  box-sizing: border-box; }

body {
  background-color: #23272a;
  color: #86c332;
  font-family: Monaco, "Lucida Console", monospace;
  font-size: 14px;
  margin: 0px; }

#portfolio-article {
  background-color: #23272a;
  background-image: repeating-linear-gradient(rgba(107, 110, 112, 0.1), rgba(107, 110, 112, 0.1) 1px, transparent 1px, transparent 4px); }

.hidden {
  position: absolute;
  z-index: -20; }

@media only screen and (max-width: 500px) {
  #navbar {
    border-bottom: 1px solid #60892f;
    width: 100vh;
    height: 25px;
    right: 100vw;
    transform: rotate(-90deg);
    transform-origin: 100% 0; }
    #navbar ul {
      width: 100%;
      flex-flow: row-reverse nowrap;
      justify-items: flex-end; }
    #navbar a {
      position: relative;
      top: -5px; }
    #navbar a:hover, #navbar a:focus {
      padding-top: 20px; }
  #projects, #skills {
    padding-left: 30px; } }

